import React from "react"
import { compose, withProps } from "recompose"
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
  Circle,
} from "react-google-maps"

const MapComponent = compose(
  withProps({
    googleMapURL: `https://maps.googleapis.com/maps/api/js?key=
        ${process.env.GATSBY_API}&callback=initMap`,
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withScriptjs,
  withGoogleMap
)(props => (
  <GoogleMap defaultZoom={17} center={{ lat: props.lat, lng: props.lon }}>
    {<Marker position={{ lat: props.lat, lng: props.lon }} />}
    {
      <Circle
        center={{ lat: props.myLat, lng: props.myLon }}
        radius={7}
        options={{
          fillColor: "#00f",
          fillOpacity: 1.0,
          strokeOpacity: 0.0,
        }}
      />
    }
    {
      <Circle
        center={{ lat: props.myLat, lng: props.myLon }}
        radius={props.accuracy}
        options={{
          fillColor: "#00f",
          fillOpacity: 0.2,
          strokeOpacity: 0.0,
        }}
      />
    }
  </GoogleMap>
))

class MapView extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      myLat: props.lat,
      myLon: props.lon,
      accuracy: 0,
    }
  }

  getGeoLocation = () => {
    return navigator.geolocation.getCurrentPosition(
      position => {
        console.log(position.coords)
        this.setState({
          myLat: position.coords.latitude,
          myLon: position.coords.longitude,
          accuracy: position.coords.accuracy,
        })
      },
      err => console.log(err),
      { enableHighAccuracy: true }
    )
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.getGeoLocation(), 1000)
  }

  componentWillUnmount() {
    clearInterval(this.timerID)
  }

  render() {
    return (
      <MapComponent
        lat={this.props.lat}
        lon={this.props.lon}
        myLat={this.state.myLat}
        myLon={this.state.myLon}
        accuracy={this.state.accuracy}
      />
    )
  }
}

export default MapView
